<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="旅行社" prop="userId">
      <el-select
        v-model="ruleForm.userId"
        filterable
        remote
        reserve-keyword
        placeholder="输入旅行社名称：模糊查询"
        :remote-method="travelRemoteMethod"
        :loading="loading">
        <el-option
          v-for="item in placeOption"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="金额/元" prop="income">
      <el-input v-model.number="ruleForm.income"></el-input>
    </el-form-item>
    <el-form-item label="充值时间" prop="rechargeTime">
      <el-date-picker
        v-model="ruleForm.rechargeTime"
        type="datetime"
        placeholder="选择日期时间">
      </el-date-picker>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'PrepayAdd',
    data() {
      return {
        loading: false,
        ruleForm: {
          userId: '',
          income: '',
          rechargeTime: ''
        },
        rules: {
          userId: [{required: true, message: '请输入名称', trigger: 'blur'}],
          income: [{required: true, message: '请输入金额', trigger: 'blur'}],
          rechargeTime: [{required: true, message: '请输入充值时间', trigger: 'blur'}]
        },
        id: '',
        placeOption: []
      }
    },
    beforeMount() {
      let that = this
      that.ruleForm.userId = that.$route.params.userId
      that.row = that.$route.params.row
      if (that.ruleForm.userId) {
        that.placeOption = [{value: that.row.id, label: that.row.name}]
      }
    },
    methods: {
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let form = {
              id: that.id || '',
              userId: that.ruleForm.userId,
              income: that.ruleForm.income,
              rechargeTime: that.ruleForm.rechargeTime,
              status: -1
            }
            console.log(form)
            let url = '/admin/prepay/add'
            if (that.id) {
              url = '/admin/prepay/edit'
            }
            that.$axios.post(url, form).then(function (res) {
              if (res.status === 200 && res.data.code === 200) {
                that.$message({type: 'success', message: '保存成功'})
                that.$router.push({name: 'PrepayList'}) // 跳转列表页
              } else {
                that.$message({type: 'error', message: '添加失败'})
              }
            }).catch(function (err) {
              console.log('查询err:')
              console.log(err)
              that.$message({type: 'error', message: '保存失败'})
            })
          } else {
            that.$message({type: 'error', message: '保存失败'})
          }
        })
      },
      travelRemoteMethod(travel) {
        console.log(travel)
        let that = this
        if (travel) {
          let url = '/admin/prepay/orgName/' + travel
          that.$axios.get(url).then((res) => {
            if (res.data.code === 200) {
              console.log(res.data.data)
              let arr = res.data.data
              that.placeOption = arr.map(function (o) {
                return {value: o.id, label: o.name}
              })
            } else {
              console.log(res.data)
            }
          }).catch((error) => {
            console.log(error)
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-form {
    max-width: 450px;
  }

  .el-select, .el-input {
    width: 300px;
  }
</style>



