<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="归属景区" prop="placeId">
      <el-select
        v-model="ruleForm.placeId"
        filterable
        remote
        reserve-keyword
        :disabled="setDisabled"
        placeholder="输入景区：模糊查询"
        :remote-method="placeRemoteMethod"
        :loading="loading">
        <el-option
          v-for="item in placeOption"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="分类名称" prop="name">
      <el-input v-model="ruleForm.name"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'CategoryAdd',
    data() {
      var checkName = (rule, value, callback) => {
        let that = this
        if (that.pageStatus === 'edit') {
          return callback()
        }
        if (!that.ruleForm.placeId) {
          that.ruleForm.name = ''
          return callback(new Error('请先填写归属景区'))
        }
        that.id = ''
        if (!value) {
          return callback(new Error('不能为空'))
        }
        value = value.toString().trim()
        if (!value) {
          return callback(new Error('不能为空'))
        }
        that.$axios.get('/admin/category/checkName/?pageStatus=' + that.pageStatus + '&name=' + value + '&placeId=' + that.ruleForm.placeId)
          .then(function (res) {
            if (res.status === 200 && res.data.code === 200) {
              return callback()
            } else {
              return callback(new Error(res.data.msg))
            }
          }).catch((err) => {
          return callback(err)
        })
      }
      return {
        pageStatus: 'add', // 页面默认添加 add ,edit
        loading: false,
        ruleForm: {
          placeId: '',
          name: ''
        },
        rules: {
          placeId: [
            {required: true, message: '请输入景区名称', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '请输入名称', trigger: 'blur'},
            {validator: checkName, trigger: 'blur'}
          ]
        },
        id: '',
        setDisabled: false,
        placeOption: []
      }
    },
    beforeMount() {
      let that = this
      that.pageStatus = 'add'
      that.id = that.$route.params.id
      that.row = that.$route.params.row
      if (that.id) {
        that.pageStatus = 'edit'
        that.setDisabled = true
        that.ruleForm = that.row
        that.placeOption = [{value: that.row.placeId, label: that.row.placeName}]
      }
    },
    methods: {
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let form = {
              id: that.id || '',
              placeId: that.ruleForm.placeId,
              name: that.ruleForm.name
            }
            console.log(form)
            let url = '/admin/category/add'
            if (that.id) {
              url = '/admin/category/edit'
            }
            that.$axios.post(url, form).then(function (res) {
              if (res.status === 200 && res.data.code === 200) {
                that.$message({type: 'success', message: '添加成功'})
                that.$router.push({name: 'CategoryList'}) // 跳转列表页
              } else {
                that.$message({type: 'error', message: '保存失败'})
              }
            }).catch(function (err) {
              console.log('查询err:')
              console.log(err)
              that.$message({type: 'error', message: '保存失败'})
            })
          } else {
            that.$message({type: 'error', message: '保存失败'})
          }
        })
      },
      placeRemoteMethod(place) {
        console.log(place)
        let that = this
        if (place) {
          let url = '/admin/place/name/' + place
          that.$axios.get(url).then((res) => {
            if (res.data.code === 200) {
              console.log(res.data.data)
              let arr = res.data.data
              that.placeOption = arr.map(function (o) {
                return {value: o.id, label: o.name}
              })
            } else {
              console.log(res.data)
            }
          }).catch((error) => {
            console.log(error)
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-form {
    max-width: 450px;
  }
</style>



