import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Index from '@/components/Index'
import Login from '@/components/Login'

import GuideUserList from '@/components/user/GuideUserList'
import MerchantUserList from '@/components/user/MerchantUserList'
import TravelUserList from '@/components/user/TravelUserList'
import VisitorUserList from '@/components/user/VisitorUserList'
import TravelUserAdd from '@/components/user/TravelUserAdd'
import PrepayList from '@/components/user/PrepayList'
import PrepayAdd from '@/components/user/PrepayAdd'

import Password from '@/components/Password'

import PlaceList from '@/components/place/PlaceList'
import PlaceAdd from '@/components/place/PlaceAdd'
import PlaceUserList from '@/components/place/PlaceUserList'
import PlaceUserAdd from '@/components/place/PlaceUserAdd'
import CategoryList from '@/components/billing/CategoryList'
import CategoryAdd from '@/components/billing/CategoryAdd'
import BillingList from '@/components/billing/BillingList'
import BillingAdd from '@/components/billing/BillingAdd'

import OrderList from '@/components/order/OrderList'
import OrderAdd from '@/components/order/OrderAdd'

import Income from '@/components/statistics/Income'
import Merchant from '@/components/statistics/Merchant'
import People from '@/components/statistics/People'

Vue.use(Router)

let router = new Router({
  linkActiveClass: 'active',
  routes: [
    {path: '/login', name: 'Login', component: Login},
    {
      name: 'Main',
      path: '/',
      component: Main,
      children: [
        {path: '/index', name: 'Index', component: Index},
        {path: 'PlaceList', name: 'PlaceList', component: PlaceList}, // 景区列表
        {path: 'PlaceAdd', name: 'PlaceAdd', component: PlaceAdd}, // 景区添加
        {path: 'PlaceUserList', name: 'PlaceUserList', component: PlaceUserList}, // 景区人员列表
        {path: 'PlaceUserAdd', name: 'PlaceUserAdd', component: PlaceUserAdd}, // 景区人员添加

        {path: 'BillingList', name: 'BillingList', component: BillingList}, // 计费模板列表
        {path: 'BillingAdd', name: 'BillingAdd', component: BillingAdd}, // 计费模板添加
        {path: 'CategoryList', name: 'CategoryList', component: CategoryList}, // 票分类列表
        {path: 'CategoryAdd', name: 'CategoryAdd', component: CategoryAdd}, // 票分类添加

        {path: 'GuideUserList', name: 'GuideUserList', component: GuideUserList}, // 导游
        {path: 'MerchantUserList', name: 'MerchantUserList', component: MerchantUserList}, // 其他商家
        {path: 'TravelUserList', name: 'TravelUserList', component: TravelUserList}, // 旅行社
        {path: 'VisitorUserList', name: 'VisitorUserList', component: VisitorUserList}, // 游客
        {path: 'TravelUserAdd', name: 'TravelUserAdd', component: TravelUserAdd}, // 旅行社添加
        {path: 'PrepayList', name: 'PrepayList', component: PrepayList}, // 预付费列表
        {path: 'PrepayAdd', name: 'PrepayAdd', component: PrepayAdd}, // 充值

        {path: 'OrderList', name: 'OrderList', component: OrderList}, // 订单列表
        {path: 'OrderAdd', name: 'OrderAdd', component: OrderAdd}, // 订单添加

        {path: 'Merchant', name: 'Merchant', component: Merchant}, // 商家票务数
        {path: 'Income', name: 'Income', component: Income}, // 票务收入
        {path: 'People', name: 'People', component: People}, // 接待人数

        // 用户
        {path: 'Password', name: 'Password', component: Password}
      ]
    }
  ]
})

router.beforeEach(({name}, from, next) => {
  if (localStorage.getItem('token')) {
    if (name === 'Login') {
      next('/index')
    } else {
      next()
    }
  } else {
    if (name === 'Login') {
      next()
    } else {
      next({name: 'Login'})
    }
  }
})

export default router



