<template>
  <div>
    <el-row :gutter="10">
      <el-input v-model="name" placeholder="名称"></el-input>
      <el-button type="primary" icon="el-icon-search" @click="search">查询</el-button>
      <router-link :to="{name:'TravelUserAdd'}">
        <el-button type="primary" icon="el-icon-circle-plus" @click="search">添加</el-button>
      </router-link>
    </el-row>
    <hr>
    <el-table :data="tableData">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="旅行社地址：">
              <span>{{ props.row.orgAddr }}</span>
            </el-form-item>
            <el-form-item label="性别：">
              <span>{{ props.row.sex || formatSex }}</span>
            </el-form-item>
            <el-form-item label="统一社会信用代码：">
              <span>{{ props.row.creditCode }}</span>
            </el-form-item>
            <el-form-item label="审核人：">
              <span>{{ props.row.auditorName }}</span>
            </el-form-item>
            <el-form-item label="创建日期：">
              <span>{{ props.row.time }}</span>
            </el-form-item>
            <el-form-item label="审核日期：">
              <span>{{ props.row.auditTime }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="名称"></el-table-column>
      <el-table-column prop="mobile" label="手机"></el-table-column>
      <!--<el-table-column prop="sex" :formatter="formatSex" label="性别"></el-table-column>-->
      <!--<el-table-column prop="orgAddr" label="旅行社地址"></el-table-column>-->
      <!-- <el-table-column prop="creditCode" label="统一社会信用代码"></el-table-column>-->
      <el-table-column prop="isPrepay" :formatter="formatPrepay" label="是否预付费"></el-table-column>
      <el-table-column prop="prepay" label="累计金额"></el-table-column>
      <el-table-column prop="balance" label="余额"></el-table-column>
      <el-table-column label="营业执照照片">
        <template slot-scope="scope">
          <a :href="scope.row.authPic" target="_blank">
            <img :src="scope.row.authPic" style="max-width: 100px;height: 50px"/>
          </a>
        </template>
      </el-table-column>
      <el-table-column prop="status" :formatter="formatStatus" label="审核状态"></el-table-column>
      <!--<el-table-column prop="auditorName" label="审核人"></el-table-column>-->
      <!--<el-table-column prop="auditTime" label="审核日期"></el-table-column>-->
      <!--<el-table-column prop="time" label="创建日期"></el-table-column>-->
      <el-table-column width="200" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="authStatus(scope.row.id, scope.$index, 'pass')">通过</el-button>
          <el-button type="danger" size="small" @click="authStatus(scope.row.id, scope.$index, 'nopass')">拒绝</el-button>
          <router-link :to="{name:'TravelUserAdd',params:{id: scope.row.id, row: scope.row}}">
            <el-button type="warning" size="small">编辑</el-button>
          </router-link>
          <router-link :to="{name:'Password',params:{id: scope.row.id, mobile: scope.row.mobile}}">
            <el-button type="primary" size="small" title="修改密码">改密</el-button>
          </router-link>
          <router-link :to="{name:'PrepayAdd',params:{userId: scope.row.id, row:scope.row}}">
            <el-button type="danger" size="small">充值</el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <pagination v-on:getPageData="getTablePageData" :total-num="totalNum"></pagination>
  </div>
</template>

<script>
  export default {
    name: 'TravelUserList',
    data() {
      return {
        name: '',
        tableData: [],
        totalNum: 0
      }
    },
    filters: {
      formatSex: function (value) {
        let text = ''
        let sex = value
        if (sex === 1) {
          text = '男'
        } else if (sex === 2) {
          text = '女'
        } else if (sex === 3) {
          text = '其他'
        }
        return text
      }
    },
    methods: {
      getTablePageData(pagerObj) {
        let params = {
          name: this.name,
          currentPage: pagerObj.currentPage,
          pageSize: pagerObj.pageSize
        }
        this.query(this, params)
      },
      search() {
        let params = {
          name: this.name
        }
        this.query(this, params)
      },
      authStatus(val, index, status) {
        let that = this
        if (!val || !status) {
          return that.$message({type: 'info', message: '此条数据有错误'})
        }
        that.$confirm('请仔细审核, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.$axios.get('/admin/user/auth?id=' + val + '&status=' + status).then(function (res) {
            if (res.status === 200 && res.data.code === 200) {
              if (status === 'pass') {
                that.tableData[index].status = 1
              } else {
                that.tableData[index].status = 5
              }
              that.$message({type: 'success', message: '提交成功!'})
            } else {
              that.$message({type: 'error', message: '提交失败'})
            }
          })
        }).catch(() => {
          that.$message({type: 'info', message: '已取消提交'})
        })
      },
      formatStatus(row, column, cellValue) {
        let text = ''
        let status = row.status
        if (status === 1) {
          text = '通过'
        } else if (status === 5) {
          text = '拒绝'
        } else {
          text = '提交审核'
        }
        return text
      },
      formatPrepay(row, column, cellValue) {
        let text = ''
        let s = row.isPrepay
        if (s === 1) {
          text = '是'
        } else {
          text = '否'
        }
        return text
      },
      query(that, params) {
        console.log(params)
        that.$axios.get('/admin/user/travelUserList', {params: params}).then(function (res) {
          console.log(`查询ok`)
          if (res.status === 200 && res.data.code === 200) {
            that.tableData = res.data.data.tableData
            that.totalNum = res.data.data.totalNum
          } else {
            that.tableData = []
            that.totalNum = 0
          }
        }).catch((error) => {
          console.log(`查询err: ${error}`)
          that.tableData = []
          that.totalNum = 0
        })
      }
    },
    mounted() {
      this.query(this, {})
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-input {
    display: inline-block;
    width: 140px;
  }

  .el-select {
    width: 100px;
  }

  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

</style>



