<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

    <el-form-item label="手机号" prop="mobile">
      <el-input v-model="ruleForm.mobile"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="password">
      <el-input v-model="ruleForm.password" type="password" :disabled="setDisabled" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="姓名" prop="name">
      <el-input v-model="ruleForm.name"></el-input>
    </el-form-item>
    <el-form-item label="性别" prop="sex">
      <el-select v-model="ruleForm.sex" placeholder="">
        <el-option
          v-for="item in optionsSex"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="状态" prop="status">
      <el-select v-model="ruleForm.status" placeholder="">
        <el-option
          v-for="item in optionsStatus"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="归属景区" prop="parent">
      <el-select
        v-model="ruleForm.parent"
        filterable
        remote
        reserve-keyword
        placeholder="输入景区：模糊查询"
        :remote-method="placeRemoteMethod"
        :loading="loading">
        <el-option
          v-for="item in placeOption"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'PlaceUserAdd',
    data() {
      var checkMobile = (rule, value, callback) => {
        let that = this
        if (!value) {
          return callback(new Error('手机号不能为空'))
        }
        let mobile = value.toString()
        if (!/^1\d{10}$/.test(mobile)) {
          return callback(new Error('填写正确的手机号'))
        }
        that.$axios.get('/admin/user/checkMobile/?pageStatus=' + that.pageStatus + '&mobile=' + mobile).then(function (res) {
          if (res.status === 200 && res.data.code === 200) {
            return callback()
          } else {
            return callback(new Error(res.data.msg))
          }
        }).catch((err) => {
          return callback(err)
        })
      }
      var checkPassword = (rule, value, callback) => {
        let that = this
        if (that.pageStatus === 'edit') {
          return callback()
        }
        if (!value) {
          return callback(new Error('密码不能为空'))
        }

        if (value.length < 8 || value.length > 20) {
          return callback(new Error('密码长度在 8 - 20 位'))
        } else {
          return callback()
        }
      }
      return {
        pageStatus: 'add', // 页面默认添加 add ,edit
        loading: false,
        ruleForm: {
          parent: '',
          mobile: '',
          password: '',
          name: '',
          sex: '',
          status: ''
        },
        rules: {
          parent: [
            {required: true, message: '请输入名称', trigger: 'blur'}
          ],
          mobile: [
            {required: true, message: '请输入手机号', trigger: 'blur'},
            {validator: checkMobile, trigger: 'blur'}
          ],
          password: [
            {validator: checkPassword, trigger: 'blur'}
          ],
          name: [
            {required: true, message: '请输入姓名', trigger: 'blur'}
          ],
          sex: [
            {required: true, message: '请选择性别', trigger: 'blur'}
          ],
          status: [
            {required: true, message: '请选择状态', trigger: 'blur'}
          ]
        },
        id: '',
        setDisabled: false,
        placeOption: [],
        optionsSex: [{
          value: 1,
          label: '男'
        }, {
          value: 2,
          label: '女'
        }, {
          value: 3,
          label: '其他'
        }],
        optionsStatus: [{
          value: 1,
          label: '在岗启用'
        }, {
          value: 5,
          label: '离岗禁用'
        }]
      }
    },
    beforeMount() {
      let that = this
      that.pageStatus = 'add'
      that.id = that.$route.params.id
      that.row = that.$route.params.row
      if (that.id) {
        that.pageStatus = 'edit'
        that.setDisabled = true
        that.ruleForm = that.row
        that.placeOption = [{value: that.row.parent, label: that.row.parentName}]
      }
    },
    methods: {
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let form = {
              id: that.id || '',
              parent: that.ruleForm.parent,
              mobile: that.ruleForm.mobile,
              password: that.ruleForm.password,
              name: that.ruleForm.name,
              sex: that.ruleForm.sex,
              status: that.ruleForm.status
            }
            console.log(form)
            let url = '/admin/user/placeAdd'
            if (that.id) {
              url = '/admin/user/edit'
            }
            that.$axios.post(url, form).then(function (res) {
              if (res.status === 200 && res.data.code === 200) {
                that.$message({type: 'success', message: '保存成功'})
                that.$router.push({name: 'PlaceUserList'}) // 跳转列表页
              } else {
                that.$message({type: 'error', message: '添加失败'})
              }
            }).catch(function (err) {
              console.log('查询err:')
              console.log(err)
              that.$message({type: 'error', message: '保存失败'})
            })
          } else {
            that.$message({type: 'error', message: '保存失败'})
          }
        })
      },
      placeRemoteMethod(place) {
        console.log(place)
        let that = this
        if (place) {
          let url = '/admin/place/name/' + place
          that.$axios.get(url).then((res) => {
            if (res.data.code === 200) {
              console.log(res.data.data)
              let arr = res.data.data
              that.placeOption = arr.map(function (o) {
                return {value: o.id, label: o.name}
              })
            } else {
              console.log(res.data)
            }
          }).catch((error) => {
            console.log(error)
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-form {
    max-width: 450px;
  }
</style>



