<template>
  <div>
    <el-row :gutter="10">
      <el-date-picker
        v-model="createdAt"
        type="daterange"
        :clearable=false
        unlink-panels
        range-separator='-'
        :editable=false
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions2">
      </el-date-picker>
      <el-select v-model="userId" placeholder="青选择商家或导游">
        <el-option
          v-for="item in userIdOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button type="primary" icon="el-icon-search" @click="search">查询</el-button>
    </el-row>
    <hr>
    <el-tag>统计指标：按订单预约入园时间且支付成功</el-tag>
    <div id="echart1" :style="{width: '100%', height: '300px'}"></div>
  </div>
</template>
<script>
  import echarts from 'echarts'
  export default {
    name: '',
    data() {
      return {
        loading: false,
        pickerOptions2: {
          shortcuts: [{
            text: '最近7天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '最近30天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '最近90天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }]
        },
        createdAt: [], // 时间
        charts: '',
        userId: '',
        userIdOptions: []
      }
    },
    methods: {
      search() {
        let that = this
        that.query()
      },
      insertOption(data) {
        let option = {
          tooltip: {
            trigger: 'axis'
          },
          title: {
            text: ''
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: data.map(function (o) {
              return o.time
            })
          },
          yAxis: {
            type: 'value',
            name: '张'
          },
          series: [{
            type: 'line',
            data: data.map(function (o) {
              return o.number || 0
            })
          }]
        }
        this.lineOption = option
      },
      // 绘制折线图
      draw(id) {
        let that = this
        let option = that.lineOption
        that.charts = echarts.init(document.getElementById(id))
        that.charts.setOption(option)
        window.onresize = function () {
          echarts.init(document.getElementById(id)).resize()
        }
      },
      query() {
        let that = this
        let createAt = that.createdAt
        let userId = that.userId
        if (!userId || (createAt.length === 0)) {
          that.$message.error('请选择时间段和景区')
          return
        }
        let params = {createAt: createAt, userId: userId}
        console.log(params)
        that.$axios.get('/admin/order/merchant', {params: params}).then(function (res) {
          if (res.data.code === 200) {
            console.log(res.data.data)
            that.insertOption(res.data.data)
            that.draw('echart1')
          } else {
            that.$message.error(res.msg)
          }
        }).catch((error) => {
          console.log(`查询err: ${error}`)
          that.$message.error(error)
        })
      }
    },
    mounted() {
      let that = this
      let end = new Date()
      let start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
      that.createdAt = [start, end]
      that.$axios.get('/admin/user/merchant').then(function (res) {
        if (res.data.code === 200) {
          console.log(res.data.data)
          let data = res.data.data
          that.userIdOptions = data.map(function (item) {
            return {value: item.id, label: item.name}
          })
        } else {
          that.$message.error(res.msg)
        }
      }).catch((error) => {
        console.log(`查询err: ${error}`)
        that.$message.error(error)
      })
    }
  }
</script>
<style scoped>
  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

  #echart1 div {
    width: 100%;
  }

  canvas {
    width: 100%;
  }
</style>



