<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="130px" class="demo-ruleForm">
    <el-form-item label="归属景区" prop="placeId">
      <el-select
        v-model="ruleForm.placeId"
        filterable
        remote
        reserve-keyword
        :disabled="setDisabled"
        placeholder="输入景区模糊查询"
        :remote-method="placeRemoteMethod"
        :loading="loading">
        <el-option
          v-for="item in placeOption"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="旅行社名称" prop="userId">
      <el-select
        v-model="ruleForm.userId"
        filterable
        remote
        reserve-keyword
        :disabled="setDisabled"
        placeholder="输入旅行社名称模糊查询"
        :remote-method="travelRemoteMethod"
        :loading="loading">
        <el-option
          v-for="item in travleOption"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预约入园日期" prop="reserveDate">
      <el-date-picker
        v-model="ruleForm.reserveDate"
        type="datetime"
        placeholder="选择日期时间">
      </el-date-picker>
    </el-form-item>
    <el-form-item label="实际入园时间" prop="enterTime">
      <el-date-picker
        v-model="ruleForm.enterTime"
        type="datetime"
        placeholder="选择日期时间">
      </el-date-picker>
    </el-form-item>
    <el-form-item label="支付方式" prop="payWay">
      <el-select v-model="ruleForm.payWay">
        <el-option
          v-for="item in optionsPayWay"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="备注" prop="mark">
      <el-input v-model="ruleForm.mark" type="textarea"></el-input>
    </el-form-item>
    <el-form-item label="选票">
      <el-button type="primary" @click="getNewBill" :disabled="setDisabled" title="景区/旅行社更改后务必刷新">刷新计费</el-button>
      <span class="allfre"> 总费用：{{totalPrice}} 元</span>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="type" :formatter="formatType" label="类型" width="110"></el-table-column>
        <el-table-column prop="categoryName" label="名称"></el-table-column>
        <el-table-column prop="unit" label="单价/元"></el-table-column>
        <el-table-column prop="number" label="张数">
          <template slot-scope="scope">
            <el-input v-model.number="scope.row.number"></el-input>
          </template>
        </el-table-column>
      </el-table>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'OrderAdd',
    data() {
      return {
        pageStatus: 'add', // 页面默认添加 add ,edit
        loading: false,
        team: true,
        individual: false,
        ruleForm: {
          placeId: '',
          userId: '',
          reserveDate: '',
          enterTime: '',
          type: '',
          people: 0,
          mark: ''
        },
        rules: {
          placeId: [{required: true, message: '请输入归属景区', trigger: 'blur'}],
          userId: [{required: true, message: '请输入旅行社名称', trigger: 'blur'}],
          reserveDate: [{required: true, message: '请输入预约日期', trigger: 'blur'}]
        },
        id: '',
        setDisabled: false,
        placeOption: [],
        travleOption: [],
        optionsPayWay: [{
          value: 5,
          label: '现金'
        }],
        tableData: []
      }
    },
    beforeMount() {
      let that = this
      that.pageStatus = 'add'
      that.id = that.$route.params.id
      that.row = that.$route.params.row
      console.log(that.row)
      if (that.id) {
        that.pageStatus = 'edit'
        that.setDisabled = true
        that.ruleForm = that.row
        that.placeOption = [{value: that.row.placeId, label: that.row.placeName}]
        that.travleOption = [{value: that.row.userId, label: that.row.userName}]
        that.getOrderTicket(that.id)
      }
    },
    computed: {
      // 计算属性的 getter
      totalPrice: function () {
        // `this` 指向 vm 实例
        let that = this
        let all = 0
        for (let i = 0; i < that.tableData.length; i++) {
          if (that.tableData[i].number > 0) {
            all += that.tableData[i].unit * that.tableData[i].number
          }
        }
        return all
      }
    },
    methods: {
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let form = {
              id: that.id || '',
              placeId: that.ruleForm.placeId,
              userId: that.ruleForm.userId,
              reserveDate: that.ruleForm.reserveDate,
              enterTime: that.ruleForm.enterTime,
              payWay: that.ruleForm.payWay,
              mark: that.ruleForm.mark
            }
            let arr = []
            for (let i = 0; i < that.tableData.length; i++) {
              if (that.tableData[i].number > 0) {
                if ((that.tableData[i].type === 1) && (that.tableData[i].number < that.tableData[i].people)) {
                  that.$message({
                    type: 'error',
                    message: that.tableData[i].categoryName + '团体票至少人数' + that.tableData[i].people
                  })
                  return
                }
                arr.push(that.tableData[i])
              }
            }
            if (arr.length === 0) {
              that.$message({type: 'error', message: '请选票！'})
              return
            }
            form.tableData = arr
            console.log(form)
            let url = '/admin/order/add'
            if (that.id) {
              url = '/admin/order/edit'
            }
            that.$axios.post(url, form).then(function (res) {
              if (res.status === 200 && res.data.code === 200) {
                that.$message({type: 'success', message: '保存成功'})
                that.$router.push({name: 'OrderList'}) // 跳转列表页
              } else {
                that.$message({type: 'error', message: '添加失败'})
              }
            }).catch(function (err) {
              console.log('查询err:')
              console.log(err)
              that.$message({type: 'error', message: '保存失败'})
            })
          } else {
            that.$message({type: 'error', message: '保存失败'})
          }
        })
      },
      placeRemoteMethod(place) {
        console.log(place)
        let that = this
        if (place) {
          let url = '/admin/place/name/' + place
          that.$axios.get(url).then((res) => {
            if (res.data.code === 200) {
              console.log(res.data.data)
              let arr = res.data.data
              that.placeOption = arr.map(function (o) {
                return {value: o.id, label: o.name}
              })
            } else {
              console.log(res.data)
            }
          }).catch((error) => {
            console.log(error)
          })
        }
      },
      travelRemoteMethod(travel) {
        console.log(travel)
        let that = this
        if (!that.ruleForm.placeId) {
          return that.$message({type: 'error', message: '请先选择景区'})
        }
        if (travel) {
          let url = '/admin/user/orgName/' + travel
          that.$axios.get(url).then((res) => {
            if (res.data.code === 200) {
              console.log(res.data.data)
              let arr = res.data.data
              that.travleOption = arr.map(function (o) {
                return {value: o.id, label: o.name}
              })
            } else {
              console.log(res.data)
            }
          }).catch((error) => {
            console.log(error)
          })
        }
      },
      formatType(row, column, cellValue) {
        let text = ''
        let type = row.type
        let people = row.people
        if (type === 1) {
          text = '团体票 至少' + people + '张'
        } else {
          text = '个体票'
        }
        return text
      },
      getNewBill() {
        let that = this
        let placeId = that.ruleForm.placeId
        let userId = that.ruleForm.userId
        if (!placeId) {
          that.$message({type: 'error', message: '请选景区！'})
          return
        }
        if (!userId) {
          that.$message({type: 'error', message: '请选旅行社！'})
          return
        }
        let params = {placeId: placeId, userId: userId}
        that.$axios.get('/admin/billing/userBilling', {params: params}).then(function (res) {
          console.log(`查询ok`)
          if (res.status === 200 && res.data.code === 200) {
            that.tableData = res.data.data.tableData
          } else {
            that.tableData = []
            that.$message({type: 'error', message: '获取选票信息出错！'})
          }
        }).catch((error) => {
          console.log(`查询err: ${error}`)
          that.tableData = []
          that.$message({type: 'error', message: '获取选票信息出错！'})
        })
      },
      getOrderTicket(orderId) {
        let that = this
        that.$axios.get('/admin/order/editGetTicket', {params: {orderId: orderId}}).then(function (res) {
          console.log(`查询ok`)
          if (res.status === 200 && res.data.code === 200) {
            that.tableData = res.data.data
          } else {
            that.tableData = []
            that.$message({type: 'error', message: '获取选票信息出错！'})
          }
        }).catch((error) => {
          console.log(`查询err: ${error}`)
          that.tableData = []
          that.$message({type: 'error', message: '获取选票信息出错！'})
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-form {
    max-width: 500px;
  }

  .allfre {
    height: 30px;
    color: #f66;
    font-size: 16px;
  }
</style>



